.App {
  margin: 0;
}

.AppHeader {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 42px;
  background-color: #282c34;
  color: white;
  display: flex;
  justify-content: center;
}

.AppHeader-title {
  margin: 0 10px;
  display: flex;
  align-items: center;
  height: 42px;
  font-size: 32px;
}

.AppHeader-version {
  display: flex;
  align-items: flex-end;
  height: 42px;
  font-size: 16px;
}


.Game {
  position: relative;
  margin: 32px auto;
  width: 960px;
  height: 540px;
  background-color: #efefef;
}

.Game-field {
  width: 192px;
  height: 416px;
  background-image: url(data:image/png;base64,);
}

.Game-pos-field1 {
  position: absolute;
  left: 132px;
  top: 64px;
}

.Game-pos-field2 {
  position: absolute;
  right: 132px;
  top: 64px;
}

.Game-puyo {
  position: absolute;
  width: 32px;
  height: 32px;
  left: 32px;
  top: 32px;
}

.Game-puyo-kind-red {
  background-image: url(data:image/png;base64,);
}

.Game-puyo-kind-green {
  background-image: url(data:image/png;base64,);
}

.Game-puyo-kind-blue {
  background-image: url(data:image/png;base64,);
}

.Game-puyo-kind-yellow {
  background-image: url(data:image/png;base64,);
}

.Game-puyo-kind-ojama {
  background-image: url(data:image/png;base64,);
}

.Game-puyo-kind-blank {
  background-image: none;
}
body {
  margin: 0;
  padding: 0;
  background-color: beige;
}

